<template>
  <div class="container">
    <div class="header">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">终端</el-menu-item>
        <el-menu-item index="2">播放日程</el-menu-item>
        <el-menu-item index="3">节目管理</el-menu-item>
        <el-menu-item index="4">素材库</el-menu-item>
        <el-menu-item index="5">权限管理</el-menu-item>
        <el-menu-item index="6">帮助文档</el-menu-item>
      </el-menu>
    </div>
    <div class="content">
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      activeIndex: '1'
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
      let routers = ['/home/terminal', '/home/terminal', '/home/terminal', '/home/material', '/home/terminal', '/home/terminal']
      this.$router.push(routers[key - 1])
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



