<template>
  <div class="loginContainer">
    <p>请输入用户名和密码</p>
    <div class="formContainer">
      <el-form ref="form" :model="form" :rules="formRules" label-width="80px">
        <el-form-item size="small" label="用户名" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item size="small" label="密码" prop="pwd">
          <el-input type="password" v-model="form.pwd" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item size="small">
          <el-button type="primary" @click="onSubmit">登录</el-button>
          <el-button @click="resetFrom">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      form: {
        name: '',
        pwd: ''
      },
      formRules: {
        name: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        pwd: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    onSubmit () {
      this.$refs['form'].validate(valid => {
        if (valid) {
          if (
            (this.form.name === 'test110' && this.form.pwd === '1234') ||
            (this.form.name === 'test800' && this.form.pwd === '1234')
          ) {
            this.$router.push('/home')
          } else {
            alert('用户名或者密码错误')
          }
        } else {
        }
      })
    },
    resetFrom () {
      this.$refs['form'].resetFields()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.loginContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  p {
    margin-top: 16px;
    padding: 8px 0;
  }
}
.formContainer {
  display: flex;
}
</style>



