<template>
  <div class="loginContainer">
    <div class="operation">
      <span  @click="showUpload">添加素材</span>
      <span>搜索</span>
    </div>
    <div class="tabs">
      <el-tabs type="card">
        <el-tab-pane label="素材">
        </el-tab-pane>
        <el-tab-pane label="数据集">
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import Upload from '@/components/common/Upload'
export default {
  name: '',
  data () {
    return {
    }
  },
  methods: {
    showUpload () {
      debugger
      this.$layer.iframe({
        content: {
          content: Upload,
          parent: this,
          data: {}
        },
        area: ['800px', '600px'],
        title: '上传素材'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.operation {
  position: absolute;
  right: 5%;
  top: 7%;
  z-index: 100;
  span {
    display: inline-block;
    cursor: pointer;
  }
}
</style>



